@import url("https://fonts.googleapis.com/css?family=Roboto:300,400");

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  background: #fff;
}

.container {
  width: 1060px;
  margin: auto;
}
.padding {
  padding: 16px;
}
.right {
  text-align: right;
}

h1 {
  color: #757575;
  font-size: 25px;
  font-weight: 300;
}

::-webkit-input-placeholder {
  /* WebKit, Blink, Edge */
  color: #c4cfd4;
  font-weight: 300;
}



